const parseFormatedTimeRange = (time: string) => {
  const [fromTime, toTime] = time.split('-');
  return `${moment(fromTime, 'HH:mm').format('hh:mma')} - ${moment(
    toTime,
    'HH:mm'
  ).format('hh:mma')}`;
};

export const checkIsOpening = (openingHours: string[]) => {
  const current = moment();
  const currentIsoWeekday = current.isoWeekday();
  const strIncluded = openingHours.find(openingHour => {
    // ["1,7>07:00-21:30"]
    return (
      parseInt(openingHour[0]) <= currentIsoWeekday &&
      parseInt(openingHour[2]) >= currentIsoWeekday
    );
  });
  const [_, times] = (strIncluded || '').split('>');
  if (!times) {
    return false;
  } else {
    return times.split('|').reduce((res, time) => {
      const [fromTime, toTime] = time.split('-');
      return (
        res ||
        (moment(fromTime, 'HH:mm').isBefore(current, 'minute') &&
          moment(toTime, 'HH:mm').isAfter(current, 'minute'))
      );
    }, false);
  }
};
