import React, { FC, Suspense } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
// Icons
import CommentIcon from '@material-ui/icons/Comment';
import LinkIcon from '@material-ui/icons/Link';
import PlaceIcon from '@material-ui/icons/Place';
import ClockIcon from '@material-ui/icons/AccessTime';
// Icons for action List
import MapIcon from '@material-ui/icons/Map';
import CallIcon from '@material-ui/icons/Call';
import ShareIcon from '@material-ui/icons/Share';
import ReportIcon from '@material-ui/icons/RecordVoiceOver';
import BookmarkIcon from '@material-ui/icons/Bookmark';
// Icons for industry type
import IndustryCateringIcon from '@material-ui/icons/Restaurant';
import IndustryRetailIcon from '@material-ui/icons/StoreMallDirectory';
import IndustryPersonIcon from '@material-ui/icons/Person';
import IndustryCompanyIcon from '@material-ui/icons/Business';

import { ReduxStore, ShopState } from '../../store/types';
import PageFrame from 'templates/PageFrame';
import { RouteComponentProps } from 'react-router';
import { useRouter } from 'router';
import LoadingSpinner from 'components/LoadingSpinner';
import { toOpenHourStrings, checkIsOpening } from 'helpers';
import { MenuList, MenuItem } from '@material-ui/core';
import * as actions from 'store/Bookmark/actions';

const iconForIndustry = [
  <IndustryCateringIcon />,
  <IndustryRetailIcon />,
  <IndustryPersonIcon />,
  <IndustryCompanyIcon />,
];

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    openHoursList: {
      flex: '1 0 100%',
      paddingTop: '0',
      paddingBottom: '0',
    },
    openHoursItem: {
      flex: '1 0 100%',
      flexFlow: 'row nowrap',
      justifyContent: 'space-between',
      paddingTop: '0',
      paddingBottom: '0',
      minHeight: '24px',
    },
    proofPhotoCard: {
      height: 200,
      width: 200,
      flexShrink: 0,
      marginRight: theme.spacing(1),
    },
    proofPhotoCardMedia: {
      height: '100%',
      width: '100%',
    },
    proofLink: {
      wordBreak: 'break-all',
    },
  })
);

type DetailPageProps = {
  shopID: number;
};

const DetailPage: FC<DetailPageProps & RouteComponentProps> = props => {
  const route = useRouter();
  const dispatch = useDispatch();
  const { pathname } = route.location;
  const shopID = parseInt(pathname.slice(pathname.lastIndexOf('/') + 1));
  const shop =
    useSelector(
      // TODO: Remove shopID - 1 magic if shop list in store update to be Map
      (state: ReduxStore) =>
        state.shops.content.find(shop => shop.id === shopID)
    ) || ({} as ShopState);
  const classes = useStyles();
  console.log('shop:', shop);
  // const openHours = shop.openingHours.map(toOpenHourStrings);
  const openHours = [{ day: '', times: [] }];
  // console.log('open hours:', openHours);
  // console.log('is opening:', checkIsOpening(shop.openingHours));
  const callTel = () => {
    window.open(`tel:${shop.phone}`);
  };

  return (
    <PageFrame title={shop.name} side={shop.side} {...props}>
      <Suspense fallback={LoadingSpinner}>
        <Box display='flex' alignItems='center' p={1}>
          {iconForIndustry[shop.industry - 1]} {shop.industryTypeName}
        </Box>
        <Divider />
        <Box display='flex' justifyContent='space-evenly' p={2}>
          {[
            {
              icon: <MapIcon />,
              label: 'Route',
              onClick: () => undefined,
            },
            {
              icon: <CallIcon />,
              label: 'Call',
              onClick: () => window.open(`tel:${shop.phone}`),
            },
            { icon: <ShareIcon />, label: 'Share' },
            { icon: <ReportIcon />, label: 'Report' },
            {
              icon: <BookmarkIcon />,
              label: 'Bookmark',
              onClick: () => dispatch(actions.addBookmark(shop.id)),
            },
          ].map(({ icon, label, onClick }, index) => (
            <Box
              key={index}
              display='flex'
              flexDirection='column'
              alignItems='center'
            >
              <IconButton onClick={onClick}>{icon}</IconButton>
              {label}
            </Box>
          ))}
        </Box>
        <Divider />
        <Box display='flex' alignItems='center' p={1}>
          <PlaceIcon />
          <Box>{shop.address}</Box>
        </Box>
        <Divider />
        <Box display='flex' alignItems='center' p={1}>
          <ClockIcon style={{ alignSelf: 'flex-start', marginTop: '16px' }} />
          {/* TODO: Update to new UI when model schema updated */}
          <MenuList style={{ width: '100%' }}>
            <MenuItem style={{ minHeight: '12px' }}>
              <div
                style={{
                  background: checkIsOpening(shop.openingHours)
                    ? 'green'
                    : 'red',
                  color: '#fff',
                  padding: '2px 10px',
                  borderRadius: '8px',
                }}
              >
                {checkIsOpening(shop.openingHours) ? 'open' : 'closed'}
              </div>
            </MenuItem>
            {openHours.map((openHour, i) => (
              <MenuItem key={`op-${i}`} className={classes.openHoursItem}>
                <div>{openHour.day}</div>
                <div>{openHour.times.join('\n')}</div>
              </MenuItem>
            ))}
          </MenuList>
        </Box>
        <Divider />
        {shop.proofPhotoLinks.length > 0 && (
          <Box overflow='scroll'>
            <Box
              display='inline-flex'
              flexWrap='nowrap'
              alignItems='center'
              p={2}
            >
              {shop.proofPhotoLinks.map((link, index) => (
                <Card className={classes.proofPhotoCard} key={index}>
                  <CardMedia
                    className={classes.proofPhotoCardMedia}
                    component='img'
                    image={link}
                  />
                </Card>
              ))}
            </Box>
          </Box>
        )}
        <Box display='flex' p={1}>
          <CommentIcon />
          <Box>
            {shop.comments.map((comment, index) => (
              <Box key={index}>{comment}</Box>
            ))}
          </Box>
        </Box>
        <Divider />
        <Box display='flex' p={1}>
          <LinkIcon />
          <Box>
            {shop.proofEvidenceLinks.map((link, index) => (
              <Link key={index} className={classes.proofLink} href={link}>
                {link}
              </Link>
            ))}
          </Box>
        </Box>
      </Suspense>
    </PageFrame>
  );
};

export default DetailPage;
