import { ShopDBSchema, ShopState } from '../types';

export const toRenderUsage = (
  shop: ShopDBSchema,
  index: number,
  ctx: ShopDBSchema[]
): ShopState => {
  return {
    ...shop,
    id: shop.id,
    name: `${shop.name || shop.name_en}`,
    industryType: shop.industryType,
    industryTypeName: '餐廳',
    industrySubtypeList: [],
    district: shop.district,
    latlng: { lat: shop.latitude, lng: shop.longitude },
    phone: shop.phoneNo,
    openingHours: shop.openingHourList || [],
    comments: shop.commentList || [],
    proofEvidenceLinks: shop.proofEvidenceLinkList || [],
    proofPhotoLinks: shop.proofPhotoLinkList || [],
    shopPhotoLinks: shop.shopPhotoLinkList || [],
    rating: shop.rating,
    side: shop.rating > 0.5 ? 'yellow' : shop.rating < 0.5 ? 'blue' : 'neutral',
  };
};

