import { Reducer } from 'redux';
import * as types from './actionTypes';
import { AppAction, ShopState, AppLatLng } from '../types';

export interface SearchState {
  term: string;
  center: AppLatLng;
  rangeKM: number;
  result: ShopState[];
}

const searchState: Reducer<SearchState, AppAction> = (
  state = {
    term: '',
    center: { lat: 22.32, lng: 114.17 },
    rangeKM: 5,
    result: []
  },
  action
) => {
  switch (action.type) {
    case types.SET_SEARCH_TERM:
      return {
        ...state,
        term: action.payload.searchTerm,
        center: action.payload.center,
        rangeKM: action.payload.rangeKM
      };
    case types.SET_SEARCH_RESULT:
      return {
        ...state,
        result: action.payload.searchResult
      };
    default:
      return state;
  }
};

export default searchState;
