import React, { FC } from 'react';
import { RouteComponentProps, Link } from 'react-router-dom';
import { toggleNavMenu } from 'store/NavMenu/actions';
import { useSelector, useDispatch } from 'react-redux';
import cls from 'classnames';
import './style.scss';
import { ReduxStore } from 'store/types';
import { MenuList, MenuItem } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import * as assets from './assets';

const useStyles = makeStyles(theme => ({
  // TODO: put all color constants in a common file?
  title: {
    paddingLeft: '20px',
    paddingTop: '20px',
    paddingBottom: '10px',
    color: '#FFB331',
    borderBottom: '1px solid #D8D8D8',
    marginBottom: '0',
  },
  menuList: {
    color: '#8E8E93',
    padding: '0',
  },
  menuItem: {
    paddingTop: '15px',
    paddingBottom: '15px',
    borderBottom: '1px solid #D8D8D8',
    fontSize: '18px',
  },
  icon: {
    paddingRight: '10px',
  }
}));

const Navigation: FC<RouteComponentProps> = props => {
  const dispatch = useDispatch();
  const store = useSelector((state: ReduxStore) => state.navMenu);
  const classes = useStyles();
  const itemClick = (route: string) => () => {
    dispatch(toggleNavMenu());
    props.history.push(route);
  };
  return (
    <div
      className={cls('Navigation', {
        'Navigation--hidden': !store.isOpen,
      })}
    >
      <h1 className={classes.title}>自由篩</h1>
      <MenuList className={classes.menuList}>
        <MenuItem className={classes.menuItem} onClick={itemClick('/bookmarks')}>
          <img className={classes.icon} src={assets.bookmarkIconUrl} srcSet={assets.bookmarkIconSrcSet} />收藏
        </MenuItem>
      </MenuList>
    </div>
  );
};

export default Navigation;
