import React, { FC } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import MenuIcon from '@material-ui/icons/Menu';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import { toggleNavMenu } from 'store/NavMenu/actions';
import './style.scss';
import { IconButton, Icon, makeStyles, Slide, Theme } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import Navigation from 'components/Navigation';
import { SideColor } from 'store/types';
import cls from 'classnames';

const SideColors = {
  yellow: 'rgb(244, 181, 79)',
  blue: 'rgb(43, 16, 172)',
  neutral: 'rgb(146, 146, 138)',
};

export interface PageFrameProps extends RouteComponentProps {
  title: string;
  side?: SideColor;
}

const useStyleInjectSide = makeStyles(theme => ({
  iconButton: {
    fontSize: '20px',
    color: '#e7e7e7',
  },
  toolbar: {
    color: '#000',
  },
  title: {
    width: '100%',
    textAlign: 'center',
  },
}));

const PageFrame: FC<PageFrameProps> = props => {
  const { children, title, side = 'neutral', ...router } = props;
  const Classes = useStyleInjectSide();
  const dispatch = useDispatch();
  return (
    <Slide direction='left' in mountOnEnter unmountOnExit>
      <div>
        <CssBaseline />
        <AppBar>
          <Navigation {...props} />
          <Toolbar className={cls(Classes.toolbar, `side--${side}`)}>
            <IconButton
              onClick={e => router.history.goBack()}
              edge='start'
              className={Classes.iconButton}
            >
              <Icon className='icon-back' />
            </IconButton>
            <Typography className={Classes.title} variant='h6'>
              {title}
            </Typography>
          </Toolbar>
        </AppBar>
        <Toolbar />
        <Container>
          <Box my={2}>{children}</Box>
        </Container>
      </div>
    </Slide>
  );
};

export default PageFrame;
