import React, { FC, RefObject } from 'react';
import { Map, TileLayer, Marker } from 'react-leaflet';
import { ShopState, AppLatLng } from '../../store/types';
import { closeNavMenu } from 'store/NavMenu/actions';
import { useDispatch } from 'react-redux';
import { iconMyLocation, iconGrey, iconYellow, iconBlue } from './assets';

type Props = {
  shops: ShopState[];
  selectedShop: ShopState | null;
  onPinPress: (shop: ShopState) => void;
  userLatLng: AppLatLng | null;
  position: {
    center: AppLatLng;
    zoom: number;
  };
  setPosition: React.Dispatch<
    React.SetStateAction<{
      center: AppLatLng;
      zoom: number;
    }>
  >;
  onMoved: () => void;
  mapRef: RefObject<any>;
};

const MapComponent: FC<Props> = props => {
  const {
    shops,
    selectedShop,
    onPinPress,
    mapRef,
    onMoved,
    position,
    setPosition,
    userLatLng,
  } = props;
  const dispatch = useDispatch();
  return (
    <Map
      ref={mapRef}
      center={position.center}
      zoom={position.zoom}
      zoomControl={false}
      onViewportChanged={v => {
        if (v.center && v.zoom) {
          setPosition({
            center: { lat: v.center[0], lng: v.center[1] },
            zoom: v.zoom,
          });
          onMoved();
        }
      }}
      onclick={() => {
        dispatch(closeNavMenu());
      }}
      style={{
        width: '100vw',
        height: '100vh',
      }}
    >
      <TileLayer
        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        url='https://{s}-tiles.locationiq.com/v2/obk/r/{z}/{x}/{y}.png?key=5e62082062ad0b'
      />
      {userLatLng && (
        <Marker
          position={userLatLng}
          icon={iconMyLocation}
          zIndexOffset={1000}
        >
          {/* <Popup>Here</Popup> */}
        </Marker>
      )}

      {shops.map(shop => {
        const { id, latlng, rating } = shop;
        const selected = !!selectedShop && id === selectedShop.id;
        let icons = iconGrey;
        if (rating < 0.45) {
          icons = iconYellow;
        } else if (rating > 0.55) {
          icons = iconBlue;
        }
        return (
          <Marker
            key={id}
            position={latlng}
            icon={icons[selected ? 1 : 0]}
            onclick={() => onPinPress(shop)}
          />
        );
      })}
    </Map>
  );
};

export default MapComponent;
