import React, { FC, useState, useEffect, useRef, useCallback } from 'react';
import clsx from 'clsx';
import { useGeolocation } from 'react-use';
import * as searchActions from '../../store/Search/actions';
import { ReduxStore, ShopState, AppLatLng } from '../../store/types';
import { useSelector, useDispatch, shallowEqual } from 'react-redux';
import 'leaflet/dist/leaflet.css';
import { Box, Button, makeStyles, Fab } from '@material-ui/core';

// Leaflet map
import { Map } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';

// Map
import MapComponent from './MapComponent';

// Search bar
import SearchBar from './SearchBar';

import Swiper, { SwiperInstance } from 'react-id-swiper';
import 'react-id-swiper/lib/styles/scss/swiper.scss';
import ShopInfoCard from 'components/ShopInfoCard';

const useStyles = makeStyles(theme => ({
  overlayElement: {
    zIndex: 500,
  },
  searchThisAreaButton: {
    marginTop: theme.spacing(1),
    color: '#fff'
  },
  fab: {
    marginLeft: 'auto',
    marginTop: theme.spacing(2),
    backgroundColor: '#fff'
  },
  fabIcon: {
    fontSize: '24px',
    color: theme.palette.primary.main
  },
  searchResultCardContainer: {
    overflowY: 'visible!important' as any,
  },
}));

//retrieve default lat lng from env
const latlngStr = process.env.REACT_APP_LATLNG || '22.28552,114.15769';
const defaultCenter: AppLatLng = {
  lat: parseFloat(latlngStr.split(',')[0]),
  lng: parseFloat(latlngStr.split(',')[1]),
};

const MapView: FC = props => {
  // Styles
  const classes = useStyles();

  // Dispatch
  const dispatch = useDispatch();

  // Map ref object
  let mapRef = useRef<Map>(null);

  // Swiper ref object to scroll programmatically
  const swiperRef = useRef<SwiperInstance>(null);

  // Current slide index
  const [slideIndex, setSlideIndex] = useState(0);
  // use user geolocation. will ask for permission here
  const { latitude: userLat, longitude: userLng } = useGeolocation({
    maximumAge: 1000,
  });
  const userLatLng = userLat && userLng ? { lat: userLat, lng: userLng } : null;

  // State to store position info, default to default center
  const [position, setPosition] = useState({ center: defaultCenter, zoom: 15 });

  // If user have moved the map, "search this area will appear"
  const [moved, setMoved] = useState(false);

  // Local searchTerm state
  const [searchTerm, setSearchTerm] = useState('');
  // Handle Input change: change local state first
  const handleSearchTermChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
  };

  // When user press "GPS" button
  const handleGPSPress = useCallback(() => {
    if (userLatLng)
      setPosition(position => ({
        ...position,
        center: userLatLng,
      }));
  }, [userLatLng]);

  // Search result
  const shops = useSelector((state: ReduxStore) => state.searchState.result);
  const bookmarkShopIds = useSelector(
    (state: ReduxStore) => state.bookmark.shopIds
  );

  // Dispatch searching with searchTerm and current map center
  const handleQuery = useCallback(() => {
    if (mapRef.current) {
      const bounds = mapRef.current.leafletElement.getBounds();
      const range =
        bounds &&
        bounds.getNorthEast().distanceTo(bounds.getSouthWest()) / 3 / 1000;
      dispatch(searchActions.setSearchTerm(searchTerm, position.center, range));
      setMoved(false);
      setSelectedShop(null);
      setSlideIndex(0);
    }
  }, [dispatch, position.center, searchTerm]);

  // Currently selected shop
  const [selectedShop, setSelectedShop] = useState<ShopState | null>(null);

  // Once browers got user geoposition for first time,
  // Map will pan to user position, and set firstLoaded.current to true
  // After that, changing user position will not update map center
  const loadStatus = useRef({ firstLoaded: false, firstFetched: false });
  useEffect(() => {
    if (userLatLng && !loadStatus.current.firstLoaded) {
      setPosition(position => ({
        ...position,
        center: userLatLng,
      }));
      loadStatus.current.firstLoaded = true;
    }
  }, [userLatLng]);

  // This is effect is triggered by updating user location
  // and map center.
  // When user location has been loaded,
  // map position should be same as user location,
  // perform searching once and set firstFetched to true.
  // After that
  useEffect(() => {
    if (
      loadStatus.current.firstLoaded &&
      !loadStatus.current.firstFetched &&
      Object.is(position.center, userLatLng)
    ) {
      handleQuery();
      loadStatus.current.firstFetched = true;
    }
  }, [handleQuery, position.center, userLatLng]);

  // Select shop when pin press
  const handlePinPress = (shop: ShopState) => {
    setSelectedShop(shop);
  };

  // Scroll to shop when shop selected
  useEffect(() => {
    if (swiperRef.current && selectedShop) {
      swiperRef.current.slideTo(
        shops.findIndex(({ id }) => id === selectedShop.id)
      );
    }
  }, [selectedShop, shops]);

  // Store current slide index
  const handleSwiperSlide = () => {
    if (swiperRef.current) {
      setSlideIndex(swiperRef.current.activeIndex);
    }
  };

  // Select shop when slide index change
  useEffect(() => {
    setSelectedShop(shops[slideIndex]);
  }, [shops, slideIndex]);

  // Center to selected shop
  useEffect(() => {
    if (selectedShop) {
      setPosition(position => ({ ...position, center: selectedShop.latlng }));
    }
  }, [selectedShop]);

  return (
    <>
      <Box
        position='absolute'
        top={0}
        left={0}
        px={2}
        py={3}
        width='100vw'
        height='100%'
        display='flex'
        flexDirection='column'
        alignContent='stretch'
      >
        <Box>
          <SearchBar
            searchTerm={searchTerm}
            handleSearchTermChange={handleSearchTermChange}
            handleQuery={handleQuery}
            className={classes.overlayElement}
          />
        </Box>
        {moved && (
          <Box>
            <Button
              variant='contained'
              color='primary'
              className={clsx(
                classes.overlayElement,
                classes.searchThisAreaButton
              )}
              onClick={handleQuery}
            >
              Search this area
            </Button>
          </Box>
        )}
        <Box flex={1} />
        <Box ml='auto'>
          <Fab
            color="inherit"
            className={clsx(classes.overlayElement, classes.fab)}
            onClick={handleGPSPress}
          >
            <i className={clsx(classes.fabIcon, 'icon-gps')} />
          </Fab>
        </Box>
        <Box ml='auto'>
          <Fab
            color="inherit"
            className={clsx(classes.overlayElement, classes.fab)}
          >
            <i className={clsx(classes.fabIcon, 'icon-filter')} />
          </Fab>
        </Box>
        {/* TODO: Hide slider when no focus */}
        <Box mx={-2} py={1} className={classes.overlayElement}>
          <Swiper
            on={{
              slideChange: handleSwiperSlide,
            }}
            slidesPerView={1.2}
            centeredSlides
            spaceBetween={10}
            getSwiper={swiper => {
              swiperRef.current = swiper;
            }}
            shouldSwiperUpdate
          >
            {shops.map(shop => (
              <ShopInfoCard
                className={clsx(
                  classes.searchResultCardContainer,
                  classes.overlayElement
                )}
                key={shop.id}
                shop={shop}
                refLatLng={userLatLng || defaultCenter}
                bookmarked={bookmarkShopIds.includes(shop.id)}
              />
            ))}
          </Swiper>
        </Box>
      </Box>

      <MapComponent
        shops={shops}
        selectedShop={selectedShop}
        onPinPress={handlePinPress}
        userLatLng={userLatLng}
        position={position}
        setPosition={setPosition}
        mapRef={mapRef}
        onMoved={() => setMoved(true)}
      />
    </>
  );
};

export default MapView;
